'use client';

import Image from 'next/image';

export function ProductItem({ image, name, color, product, onSelect }) {
    return (
        <div className="bg-white p-6 rounded-lg shadow-md max-w-sm text-center">
            <div className="w-[150px] h-[150px] overflow-hidden rounded">
                <Image src={image} alt={name} width={150} height={150} className="object-cover" />
            </div>
            <h4 className="text-xl font-semibold mt-4">
                <button onClick={() => {onSelect(product)}}>{name}</button>
            </h4>
            <p className="text-gray-700 mt-2">Color: {color}</p>
        </div>
    );
}
